/*global kakao*/
import React, { useEffect, useState, useRef } from "react";
import { Form } from "react-bootstrap";
import { Map, MapMarker } from "react-kakao-maps-sdk";
import { useDispatch, useSelector } from "react-redux";
import {
  setSearchMap,
  setMarkers,
  setInfo,
  setAddress,
  setPopShow,
} from "../store/store.js";
import OverlayTrigger from "react-bootstrap/OverlayTrigger";
import Popover from "react-bootstrap/Popover";
import TextField from "@mui/material/TextField";
import Typography from "@mui/material/Typography";
import { makeStyles } from "@mui/styles";

function KaKaoMap() {
  const { kakao } = window;
  const [map, setMap] = useState();
  const { searchMap, markers, info, address, popShow } = useSelector(
    (state) => state
  );
  const dispatch = useDispatch();
  const search = useRef(null);
  const placename = useRef(null);

  const useStyles = makeStyles(() => ({
    form: {
      paddingBottom: "100px",
    },
    uploadButton: {
      width: "100% !important",
    },
    box: {
      margin: "15px 0",
    },
    popper: {
      padding: "0",
    },
  }));

  const classes = useStyles();

  useEffect(() => {
    if (!map) return;
    const ps = new kakao.maps.services.Places();

    if (searchMap != "") {
      ps.keywordSearch(searchMap, (data, status, _pagination) => {
        if (status === kakao.maps.services.Status.OK) {
          const bounds = new kakao.maps.LatLngBounds();
          let markers = [];

          for (var i = 0; i < data.length; i++) {
            // @ts-ignore
            markers.push({
              position: {
                lat: data[i].y,
                lng: data[i].x,
              },
              content: data[i].place_name,
            });
            // @ts-ignore
            bounds.extend(new kakao.maps.LatLng(data[i].y, data[i].x));
          }
          dispatch(setMarkers(markers));

          //       
          map.setBounds(bounds);
          dispatch(setAddress(data));
        }
      });
    }
  }, [searchMap]);

  const [anchorEl, setAnchorEl] = useState(null);

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  const mapChange = (e) => {
    dispatch(setSearchMap(e.target.value));
    if (search.current.value == "") {
      dispatch(setPopShow(false));
    } else {
      dispatch(setPopShow(true));
    }
  };

  const popover = (
    <Popover id="popover-basic">
      <Popover.Body className={classes.popper}>
        {address.map((data, index) => {
          return (
            <ul className="address-list-wrap" key={index}>
              <li
                onClick={(e) => {
                  dispatch(setInfo(data));
                  dispatch(setPopShow(false));
                  search.current.value = data.place_name;
                }}
                ref={placename}
              >
                {data.place_name}
              </li>
              <li>{data.address_name}</li>
              <li>
                <a href={data.place_url}> {data.place_url}</a>
              </li>
            </ul>
          );
        })}
      </Popover.Body>
    </Popover>
  );

  return (
    <>
      <Form.Group>
        <OverlayTrigger
          show={popShow}
          trigger="click"
          placement="bottom-start"
          overlay={popover}
        >
          <TextField
            fullWidth
            label=""
            id="fullWidth"
            onChange={mapChange}
            inputRef={search}
            aria-describedby={id}
            className={classes.box}
            defaultValue={info.place_name}
          />
        </OverlayTrigger>
      </Form.Group>
      <Map
        center={{
          lat: 37.566826,
          lng: 126.9786567,
        }}
        style={{
          width: "100%",
          height: "350px",
          zIndex: 0,
          margin: "15px 0",
        }}
        level={6}
        onCreate={setMap}
      >
        {markers.map((marker) => (
          <MapMarker
            key={`marker-${marker.content}-${marker.position.lat},${marker.position.lng}`}
            position={marker.position}
            style={{ backgroundColor: "#ff0000" }}
            image={{
              src: "https://xkaizew.hgodo.com/uploads/marker.png", //  
              size: {
                width: 24,
                height: 30,
              }, //  
              options: {
                offset: {
                  x: 27,
                  y: 69,
                }, //  .       .
              },
            }}
            onClick={(e) => {
              dispatch(setInfo(marker));
              console.log(marker);
              search.current.value = marker.content;
            }}
          >
            {info && info.content === marker.content && (
              <div
                style={{
                  color: "#000",
                  backgroundColor: "#90caf9",
                  borderRadius: "15px",
                  fontSize: "0.9rem",
                  padding: "10px 25px",
                }}
              >
                {marker.content}
              </div>
            )}
          </MapMarker>
        ))}
      </Map>
    </>
  );
}

export default KaKaoMap;
